import gsap from 'gsap';
import './style.css';
import { ScrollTrigger } from 'gsap/src/ScrollTrigger';
import{ MotionPathPlugin }from 'gsap/MotionPathPlugin';
gsap.registerPlugin(ScrollTrigger,MotionPathPlugin)

gsap.to(
  ".alignn",{
y:100,
rotateY: -150,
rotation:-100,
duration: 360,

    motionPath: {
      path: ".svgg",
      align: ".svgg",
      alignOrigin: [0.5, 0.5],
      end:0.5,
    },
  
    scrollTrigger:{
      trigger:".svgg",
      scrub:2,
      start:"top top",
      end:"bottom bottom",
      markers:true,
    
    }
  
  }
)